.container {
  width: 75%;
  margin: auto;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
}

@media (min-width: 40rem) {
  .container {
    flex-direction: row;
    align-items: flex-start;
    flex-wrap: wrap;
  }
}
