.container {
  padding: 1rem;
  margin: 2rem;
  width: 14rem;
  height: 28rem;
  display: flex;
  border-radius: 0.25rem;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: #fff;
}

.container h3 {
  align-self: flex-start;
}

.container p {
  color: #878787;
}

.image {
  width: 100%;
}

.readMoreBtn {
  align-self: flex-start;
  margin-top: auto;
  background-color: transparent;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  border: 2px solid #000;
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.readMoreBtn:hover {
  color: #fff;
  background-color: #000;
}

.tagContainer {
  display: flex;
  flex-direction: row;
  align-self: flex-start;
  gap: 0.2rem;
}

.tags {
  line-height: 1.5;
  font-weight: 500;
  font-size: 0.9rem;
  padding: 0 0.5rem;
  background-color: #ffc107;
  margin-bottom: 0;
  text-align: center;
}
